.has-slider.wp-block-gallery.has-nested-images {
    display: block;
    overflow: hidden;
    position: relative;
    &.has-size-full{
        width: 100vw;
        max-width: 1800px;
        max-height: 800px;
        margin: auto;
    }
    &.has-size-large{
        max-width: 1000px;
        max-width: 800px;
        margin: auto;
    }
    &.has-size-regalur{
        max-width: 1000px;
        max-width: 800px;
        margin: auto;
    }
    &.has-size-small{
        max-width: 1000px;
        max-width: 800px;
        margin: auto;
    }
  }
  .slick-dots {
    position: absolute;
    bottom: 0;
    display: block;
    width: 100%;
    padding: 0;
    list-style: none;
    text-align: center;
  }
  .slick-slide {
    display: none;
    float: left;
    height: 100%;
    min-height: 1px;
  }
  .slick-initialized .slick-slide {
    display: block;
  }
  .slick-slide .slide__img {
    @media (min-width: 992px) {
      top: 0;
      left: 0;
      position: relative;
    }
    width: 100%;
    overflow: hidden;
    img {
      min-width: 100%;
      height: 100%;
      opacity: 1 !important;
      animation-duration: 3s;
      transition: all 1s ease;
      object-fit: cover;
      object-position: center;
      position: initial;
    }
  }
  
  .slick-nav {
    --active: #fff;
    --border: rgba(255, 255, 255, .12);
    background: rgba(241, 243, 244, .4);
    width: 44px;
    height: 44px;
    position: absolute;
    border-radius: 50%;
    padding: 0;
    cursor: pointer;
    top: calc(50% - 44px);
    &.prev-arrow {
      left: 3%;
      transform: scaleX(-1);
      z-index: 999;
    }
  &.next-arrow{
    left: auto;
    right: 3%;
  }
    i {
        display: block;
        position: absolute;
        margin: -10px 0 0 -10px;
        width: 20px;
        height: 20px;
        left: 50%;
        top: 50%;
        &:before,
        &:after {
            content: '';
            width: 10px;
            height: 2px;
            border-radius: 1px;
            position: absolute;
            left: 50%;
            top: 50%;
            background: var(--active);
            margin: -1px 0 0 -5px;
            display: block;
            transform-origin: 9px 50%;
        }
        &:before {
            transform: rotate(-40deg);
        }
        &:after {
            transform: rotate(40deg);
        }
    }
    &:before,
    &:after {
        content: '';
        display: block;
        position: absolute;
        left: 1px;
        right: 1px;
        top: 1px;
        bottom: 1px;
        border-radius: 50%;
        border: 2px solid var(--border);
    }
    svg {
        width: 44px;
        height: 44px;
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        color: var(--active);
        stroke-width: 2px;
        stroke-dashoffset: 126;
        stroke-dasharray: 126 126 0;
        transform: rotate(0deg);
    }
    &.animate {
        svg {
          -webkit-animation: stroke 1s ease forwards .3s;
            animation: strokes 1s ease forwards .3s;
        }
        i {
            animation: arrow 1.6s ease forwards;
            &:before {
                animation: arrowUp 1.6s ease forwards;
            }
            &:after {
                animation: arrowDown 1.6s ease forwards;
            }
        }
    }
  }
  .slick-dots {
    text-align: center;
    padding: 5px;
    background: #2d3042;
    margin: 0;
    li {
      display: inline-block;
      vertical-align: top;
      margin: 0 8px;
      button {
        width: 16px;
        height: 16px;
        border: none;
        cursor: pointer;
        border-radius: 50%;
        border: 2px solid #fff;
        box-shadow: 0 0 0 0 transparent;
        vertical-align: middle;
        color: #fff;
        background-color: #fff;
        transition:all .3s ease;
        opacity: .4;
        padding: 0;
        &:focus {
          outline: none;
        }
        &:hover {
          opacity: 1;
        }
      }
      &.slick-active {
        button {
          border-color: #000;
          box-shadow: 0 0 0 2px #fff;
          opacity: 1;
        }
      }
    }
}

.slick-dots li button::before {
  font-size: 15px;
}
@keyframes strokes {
    51% {
        transform: rotate(-180deg);
        stroke-dashoffset: 0;
    }
    52% {
        transform: rotate(-360deg);
        stroke-dashoffset: 0;
    }
    100% {
        transform: rotate(-180deg);
        stroke-dashoffset: 126;
    }
}

@keyframes arrow {
    0%,
    100% {
        transform: translateX(0);
        opacity: 1;
    }
    23% {
        transform: translateX(17px);
        opacity: 1;
    }
    24%,
    80% {
        transform: translateX(-22px);
        opacity: 0;
    }
    81% {
        opacity: 1;
        transform: translateX(-22px);
    }
}

@keyframes arrowUp {
    0%,
    100% {
        transform: rotate(-40deg) scaleX(1);
    }
    20%,
    80% {
        transform: rotate(0deg) scaleX(.1);
    }
}

@keyframes arrowDown {
    0%,
    100% {
        transform: rotate(40deg) scaleX(1);
    }
    20%,
    80% {
        transform: rotate(0deg) scaleX(.1);
    }
}